/* nav bar button css (lines 3 to 50) are from https://codepen.io/azureknight/pen/DbrdeB */

.linkbutton {
  width: 20%;
  height: 80px;
  background: #f3b9e8;
  cursor: pointer;
  -webkit-transition: all 0.6s ease;
  -moz-transition: all 0.6s ease;
  -o-transition: all 0.6s ease;
  transition: all 0.6s ease;
  display: inline-block;
}
    
h2 {
  float: left;
  width: 100%;
  height: 100%;
  text-align: center;
  margin-top: 25px;
  font-weight: 100;
}

&:hover {
  color: #fff;
  background: none;
}


.ease {
  width: 0;
  background: #282828;
  height: 80px;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.linkbutton:hover .ease {
  width: 100%;
}

.nohover:hover {
  color: #000;
}

.amethyst {
  background: #d7a9e9;
}

html {
  background-color: #f3b9e8;
  color: #546125;
  text-shadow: 3px 3px 3px #ababab;
  text-align: center;
}

h3 {
  float: left;
  width: 100%;
  text-align: center;
  font-weight: 100;
}

h4 {
  float: left;
  width: 100%;
  height: 75%;
  text-align: center;
  font-weight: 100;
}

header {
  min-height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(25px + 2vmin);
}

.App-link {
  color: #454E22;
}

a {
  color: inherit;
  text-decoration: none;
  font-weight: 100px;
}

.pictures {
  width: 25%;
  height: 25%;
  padding-left: 1%;
  padding-right: 1%;
}

.border {
  margin-top: -10px;
  width:100%;
}


.homePage, .aboutPage, .contactPage, .artPage, .resumePage, .projectsPage {
  padding: 250px;
}